
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import products from '@/components/Products'
const bannerId = 1
const themeIds = '1,2,3'
export default {
name: 'Home',
data() {
return {
imgList: [],
themeList: [],
recentList: []
}
},
created() {
this.getBanner()
this.getTheme()
this.getRecent()
},
methods: {
linkToDetail(id) {
this.$router.push({ path: '/page/detail', query: { id: id } })
},
linkToTheme(id) {
this.$router.push({
path: '/page/theme',
query: { id: id }
})
},
getBanner() {
this.$http
.get(
`https://www.easy-mock.com/mock/5a4896ba62de717d44f2406e/api/v1/banner/${bannerId}`
)
.then(res => {
this.imgList = res.data.data.items
})
},
getTheme() {
this.$http
.get(
`https://www.easy-mock.com/mock/5a4896ba62de717d44f2406e/api/v1/theme?ids=${themeIds}`
)
.then(res => {
this.themeList = res.data.data
})
},
getRecent() {
this.$http
.get(
'https://www.easy-mock.com/mock/5a4896ba62de717d44f2406e/api/v1/product/recent'
)
.then(res => {
this.recentList = res.data.data
})
}
},

components: {
Swiper,
SwiperItem,
products
}
}



