export default {
    name: 'products',
    props: ['products'],
    data() {
        return {}
    },
    created() {},
    methods: {
        linkToDetail(e) {
            let id = e.currentTarget.dataset.id
            this.$router.push({ path: '/page/detail', query: { id: id } })
        }
    }
}



